@import '../../style.scss';
@import '../../reset.scss';

.footer {
    padding-top: 10px;
    margin-top: 10px;
    background: $color_3;
    width: 100%;

    &__title {
        text-align: center;
        font-size: $font_size_13;
        margin: 15px 0;
    }

    &__links {
        display: flex;
        justify-content: center;

        .round_icon {
            display: block;
            height: 35px;
            width: 35px;
            margin: 0 8px;
            border-radius: 100%;
            background-repeat: no-repeat;
            background-position: center;

            &.fb {
                background-image: url('../../img/icon_fb.svg');
            }

            &.ig {
                background-image: url('../../img/icon_ig.svg');
            }
        }
    }

    &__copyright {
        text-align: center;
        font-size: $font_size_11;
        color: $color_font_2;
        padding: 15px 0;
    }
}