@import '../../style.scss';
@import '../../reset.scss';

.navbar {
    position: fixed;
    z-index: 100;
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.16);
    background-color: $color_1;
    background-image: url('../../img/icon_logo.svg');
    background-size: 80px;
    background-repeat: no-repeat;
    background-position: center;

    .burger_menu {
        display: inline-block;
        cursor: pointer;

        .bar {
            width: 22px;
            height: 3px;
            background-color: $color_2;
            margin: 4px 18px;
            transition: 0.4s;
        }
    }

    &__menu {
        position: fixed;
        z-index: 10;
        display: flex;
        flex-direction: column;
        align-items: center;
        top: 50px;
        height: 0;
        width: 100vw;
        background: $color_1;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.16);
        overflow: hidden;
        transition: .3s;

        &.on {
            margin-top: 50px;
            height: auto;
            padding: 5px 0;
        }

        &__list {
            overflow: hidden;

            &__item {
                display: block;
                text-align: center;
                width: 80vw;
                padding: 25px 0;
                font-size: $font_size_13;
                border-bottom: $color_2 1px solid;
                margin-bottom: -1px;
            }
        }
    }
}