@import '../../../style.scss';
@import '../../../reset.scss';

.latest_news {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    padding: 20px 0;
    width: 100vw;
    background: $color_3;

    &__title {
        text-align: center;
        font-size: $font_size_13;
        margin: 15px 0;
    }

    &__content {
        width: 80vw;
        overflow: hidden;

        &__item {
            margin-top: -1px;
            padding: 6px 0;
            border-top: $color_2 1px solid;

            &__title {
                font-size: $font_size_13;
                margin: 8px;
            }

            &__date {
                font-size: $font_size_11;
                margin: 8px;
            }
        }
    }
}