@import '../../../style.scss';
@import '../../../reset.scss';

.exhib_top_bar {
    position: fixed;
    z-index: 50;
    margin-top: 49px;
    left: 0;
    height: 51px;
    width: 100vw;
    display: flex;
    align-items: center;
    background: $color_3;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.16);

    &__title {
        margin-left: 5vw;
        font-size: $font_size_13;
    }

    &__mark {
        margin-left: 10px;
        background-position: center;
        background-repeat: no-repeat;
        height: 100%;
        width: 14px;
        background-size: contain;
        background-image: url('../../../img/icon_heart.svg');
    }

    &__select_page {
        position: absolute;
        right: 0;
        margin-right: 5vw;
        background: white;
        border-radius: 20px;
        padding: 4px 18px;
        border: none;
    }
}