@import '../../../style.scss';
@import '../../../reset.scss';

.exhib_info__content,
.exhib_works__content,
.exhib_team__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__category {
        display: flex;
        align-items: center;
        align-self: flex-start;
        margin-top: 20px;

        &::before {
            content: '';
            display: inline-block;
            height: 18px;
            width: 16px;
            background-color: $color_4;
            margin-right: 6px;
            font-size: $font_size_15;
        }
    }

    &__pic {
        width: 100vw;
        height: auto;

        &>img {
            width: 100%;
        }
    }

    &__team_pic {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px 0;
        width: 100vw;
        height: auto;

        &>img {
            width: 50%;
        }
    }

    &__3d_view {
        width: 100vw;
        height: auto;

        &>iframe {
            width: 100vw;
            height: calc(100vh - 100px);
        }
    }

    &__audio {
        width: 92vw;
        border-radius: 6px;
        margin-bottom: 10px;
        background: rgba(255, 255, 255, 0.95);
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.16);
        transition: .3s;

        &.float {
            position: fixed;
            top: 100px;
            margin-top: 6px;
            width: 95vw;

            .exhib_info__content__audio__close {
                display: block;
            }
        }

        &__name {
            font-size: $font_size_11;
            padding-top: 18px;
            padding-left: 14px;
        }

        &__close {
            position: absolute;
            top: 12px;
            right: 14px;
            display: none;
            height: 24px;
            width: 24px;
            background-image: url('../../../img/icon_close_blue.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        }
    }

    &__basic_info {
        width: 90vw;
        text-align: left;
        font-size: $font_size_13;
        line-height: $line_height_13;
        margin: 14px 0;

        &__no,
        &__artist {
            font-size: $font_size_11;
            line-height: $line_height_13;
            color: $color_font_2;
        }

        &__title {
            font-size: $font_size_13;
            line-height: $line_height_13;
        }
    }

    &__numbers {
        display: flex;
        justify-content: flex-start;
        width: 90vw;
        margin: 14px 0;

        &__item {
            margin-right: 14px;

            &__count {
                color: $color_4;
                font-size: $font_size_15;
                line-height: $line_height_15;
            }

            &__text {
                font-size: $font_size_13;
            }
        }
    }

    &__text {
        width: 90vw;
        text-align: left;
        font-size: $font_size_13;
        line-height: $line_height_13;
        margin: 20px 0;
    }

    &__video {
        width: 90vw;
        overflow: hidden;
        margin: 10px 0;

        &>iframe {
            width: 90vw;
            height: 52vw;
        }
    }
}

.exhib_info__work_intro {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px;
        width: 92vw;
        overflow: hidden;
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.16);
        margin: 6px 0;

        &__pic {
            height: 80px;
            width: 125px;

            &>img {
                width: 100%;
            }
        }

        &__text {
            display: flex;
            height: 80px;
            width: calc(100% - 125px - 90px);
            flex-direction: column;
            justify-content: center;
            margin-left: 20px;

            &__no {
                color: $color_font_2;
                font-size: $font_size_13;
            }

            &__title {
                font-size: $font_size_15;
                margin: 6px 0;
            }

            &__artist {
                color: $color_font_2;
                font-size: $font_size_13;
            }
        }

        &__button {
            display: block;
            height: 50px;
            width: 70px;
            background-image: url('../../../img/icon_play.svg');
            background-size: 40%;
            background-repeat: no-repeat;
            background-position: center;
            border-left: $color_3 1px solid;
        }
    }
}

.exhib_info__other_exhib {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;

    &__title {
        font-size: $font_size_13;
        margin: 14px 0;
    }
}