@import "../../../reset.scss";
@import '../../../style.scss';

.exhib_card {
    width: 92vw;
    margin: 6px 0;
    border-radius: 12px;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);

    &__pic {
        &>img {
            width: 100%;
        }
    }

    &__text {
        padding: 8px;

        &__title {
            font-size: $font_size_13;
            margin: 8px;
        }

        &__artist {
            font-size: $font_size_11;
            margin: 8px;
        }

        &__numbers {
            display: flex;
            font-size: $font_size_11;
            margin: 8px;
            color: $color_font_2;

            &__item {
                margin-right: 6px;

                &::before {
                    content: '';
                    display: inline-block;
                    margin-right: 2px;
                    background-position: center;
                    background-repeat: no-repeat;
                }

                &.view::before {
                    height: 100%;
                    width: 20px;
                    background-size: 14px;
                    background-image: url('../../../img/icon_eye.svg');
                }

                &.mark::before {
                    height: 100%;
                    width: 18px;
                    background-size: contain;
                    background-image: url('../../../img/icon_heart.svg');
                }
            }
        }
    }
}